import { Component } from '@angular/core';
import { NavController, LoadingController, AlertController } from 'ionic-angular';
import * as _ from 'lodash';
import { Storage } from '@ionic/storage';

import { ServiceProvider } from '../../providers/service/service';
import { DetailsPage } from '../../pages/details/details';

@Component({
	selector: 'page-home',
	templateUrl: 'home.html'
})
export class HomePage {

	tickerArr: any[] = [];
	tickerArrCopy: any[] = [];
	favorites: any[] = [];
	actions: string = 'top';
	global:any = {};

	constructor(public navCtrl: NavController, public DataFactory: ServiceProvider, public loadingCtrl: LoadingController, public storage: Storage, public alertCtrl: AlertController) {
		storage.get('favorites').then((val) => {
			if (!_.isEmpty(val)) {
				this.favorites = val;
			}
		});
	}

	ionViewDidLoad() {
		
	}

	doRefresh(refresher) {
		setTimeout(()=>{
			refresher.complete();
		},1000);
	}

	getItems(ev: any) {
		let val = ev.target.value;
		console.log('val: ', val)
		if (val && val.trim() != '') {
			this.tickerArr = _.filter(this.tickerArrCopy, (row: any) => {
				return row.name == val;
			})
		} else {
			this.tickerArr = this.tickerArrCopy;
		}
	}

	gotoDetails(item) {
		this.navCtrl.push(DetailsPage, {
			crypto: item
		});
	}

	addToFav(item) {
		
	}

	segmentChanged(ev) {
		
	}
}



